@use 'base/variables' as v;
@use 'base/mixins' as m;

.boton-amarillo-block{
    @include m.boton(v.$amarillo, block);
}

.boton-amarillo{
    @include m.boton(v.$amarillo, inline-block);
}

.boton-rojo-block{
    @include m.boton(v.$rojo, block);
    width: 100%;
}

.boton-verde{
    @include m.boton(v.$verde, inline-block);
}